.divider {
    height: 1px ;
    background-color: #2c2928;
    width: 100%;
}
.footer {
    position: absolute;
    bottom: 20px;
    background-color: #161616;
    width: 100%;
    height: 20%;

}
.wrapper-footer {
    margin-top: 20px;
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}

.footer .wrapper-blocks {
    display: flex;
    flex-direction: column;
}
.footer .block {
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    border: 1px solid #2c2928;
    border-radius: 5px;
    padding: 10px 15px;
    margin-left: 20px;
}
.block img{
    width: 23px;
    margin-right: 20px;
}
.footer ul {
    width: 100%;
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;
}
.footer li {
    display: flex;
    width: 100%;
    height: 25px;
    align-items: center;
}
.footer a:hover {
    color: #f4a81d;
}
.footer a {
    text-decoration: none;
    color: #626a71;
}
.footer .list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.footer .text14px {
    color: white;
    font-weight: 600;
    margin-bottom: 10px;
}
.logo {
    display: flex;
}
.logo img  {
    margin-right: 10px;
    width: 50px;
}