.header {
    display: flex;
    height: 60px;
    align-items: center;
    justify-content: space-between;
}
.header-wrapper {
    display: flex;
    height: 100%;
    align-items: center;
 }
.header-logo {
    /*margin-left: 30px;*/
    width: 36px;
}
.header-tabs {
    display: flex;
    color: #787876;
    justify-content: space-evenly;
    font-weight: 300;
    font-size: 14px;
}

.row-down{
    width: 18px;
}
.tab {
     position: relative;
 }
span {
    pointer-events: fill;
}
span:hover {
    color: white;
    transition-duration: 0.5s;
}
.light:before {
    content: '';
    margin-left: 37px;
}
.item {
    position: absolute;
    left: 20px;
    top: 40px;
    width: 250px;
    border: 1px solid #2c2928;
    border-top: none;
    background-color: #161616;
    display: flex;
    text-align: left;
    transition-duration: 0.2s;
}
.header ul {
    width: 100%;
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;
}
.header li {
    display: flex;
    width: calc(100% );
    height: 40px;
    align-items: center;
}
.header li span {
    margin-left: 17px;
}
.header li:hover {
    background-color: #222222;
}
.header a {
    text-decoration: none;
    color:#787876;
}
.connect-wallet {
    font-size: 14px;
    color: #787876;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.connect-wallet span{
    margin-right: 2px;
}
.wr-atribut {
    margin-left: 25px;
    display: flex;
}
.wr-atribut span{
    margin-top: 2px;
}
a:hover {
    color: white;
    transition-duration: 0.5s;
}
.connect-wallet .atribut{
    margin-right: 10px;
    width: 23px;
}
.connect-wallet .dots {
    width: 23px;
}
.connect-btn {
    margin-left: 25px;
    font-size: 14px;
    color: #787876;
    background-color: rgba(43,89,95,0);
    border: none;
}
.connect-btn:hover {
    color: white;
    transition-duration: 0.5s;
}
.divider {
    height: 1px ;
    background-color: #2c2928;
    width: 100%;
}
.hide {
    top: -200px;
    transition-duration: 0.2s;
}
