.wrapper {
    /*margin-top: 20px;*/
    display: flex;
    width: 100%;
    flex-direction: column;
}
.exchange {
    margin-top: 20px;
    display: flex;
    width: 100%;
    align-items: center;
}
.wrapper-exchange {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
}
.swap-ch {
    margin-top: 20px;
    display: flex;
}
.wrapper-blocks {
    display: flex;
}
.block {
    display: flex;
    align-items: center;
    border: 1px solid #2c2928;
    border-radius: 5px;
    padding: 10px 15px;
    margin-left: 20px;
}
.block img{
    width: 23px;
    margin-right: 20px;
}
.swap {
    width: 510px ;
    height: 355px;
    border: 1px solid #2c2928;
    border-radius: 5px;
    padding: 10px 30px 30px 30px;
    display: flex;
    flex-direction: column;
}
.swap-option {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between
}
.swap img   {
    width: 22px;
    margin-left: 0px;
}
.wrapper-from,.wrapper-to,.swap-from,.swap-to,.input ,.wr-dvd{
    display: flex;
    align-items: center;
    justify-content: space-between
}
.form-block {
    margin-left: 7px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.wrapper-from .icon,.wrapper-to .icon {
    width: 50px;
}
.swap-to,.swap-from {
    position: relative;
    height: 54px;
}
.swap-from {
    margin-top: 20px;
}
.input {
    background-color: #1d1d1d;
    width: 40%;
    height: 95%;
    padding: 0 15px;
}
.wr-dvd{
    height: 100%;
}
.wr-dvd .vert-divider {
    margin-right: 10px;
    height: 80%;
    width: 2px;
    background-color: #2c2928;
}
.balance {
    position: absolute;
    bottom: -17px;
    right: 0px;
}
.swap-change{
    text-align: left;
}
.swap-change .change {
    margin-left: 7px;
    width: 35px;
}
.swap-btn {
    margin-top: 45px;
    background-color: #1d1d1d;
    border: none;
    height: 50px;
    color: #595958;
    font-weight: 600;
    font-size: 19px;
    border-radius: 5px;
}
.chart {
    margin-left: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}